<template>
    <div>
        <!-- 面包屑 -->
       <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/shouye' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>权限管理</el-breadcrumb-item>
            <el-breadcrumb-item>角色列表</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- 卡片视图 -->
      <el-card>
          <!-- 添加角色按钮区 -->
          <el-row>
              <el-clo>
                  <el-button type="primary">添加角色</el-button>
              </el-clo>
          </el-row>
          <!-- 角色列表 -->
            <el-table :data="rolelist" border stripe>
                    <!-- 展开列 -->
                <el-table-column type="expand">
                        <template slot-scope="scope">
                                <el-row :class="['bdbottom',key===0?'bdtop':'']" v-for="(item,key) in scope.row.children" :key="item.id">
                                    <!-- 渲染一级权限 -->
                                    <el-col :span="5"  closable @close="remove(scope.row,item.id)">
                                        <el-tag>
                                            {{item.authName}}
                                        </el-tag>
                                        <i class="el-icon-caret-right"></i>
                                    </el-col>
                                    <!-- 渲染二级和三级权限 -->
                                    <el-col :span="19">
                                        <!-- 通过循环嵌套渲染二级权限 -->
                                        <el-row :class="[key2===0?'':'bdtop']" v-for="(item2,key2) in item.children" :key="item2.id">
                                            <el-col :span="6">
                                                <el-tag type="success"  closable @close="remove(scope.row,item2.id)">
                                                    {{item2.authName}}
                                                </el-tag>
                                                 <i class="el-icon-caret-right"></i>
                                            </el-col>
                                            <el-col :span="18">
                                                    <el-tag type="warning" v-for="(item3,key3) in item2.children" :key="item3.id" 
                                                                                closable @close="remove(scope.row,item3.id)">
                                                            {{item3.authName}}
                                                    </el-tag>
                                            </el-col>
                                        </el-row>
                                    </el-col>
                                </el-row>
                        </template>
                </el-table-column>
                    <!-- 索引列 -->
                    <el-table-column type="index"></el-table-column>
                    <el-table-column label="角色名称" prop="roleName"></el-table-column>
                    <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
                    <el-table-column label="操作" width="320px">
                        <template slot-scope="scope">       
                                <el-button size="mini" type="primary" icon="el-icon-edit">编辑</el-button>
                                <el-button size="mini" type="danger" icon="el-icon-edlete">删除</el-button>
                                <el-button size="mini" type="warning" icon="el-icon-setting" @click="showdialog(scope.row)">分配权限</el-button>
                        </template>
                    </el-table-column>
            </el-table>
、
      </el-card>

            <!-- Dialog对话框分配权限 -->
            <el-dialog
                    title="分配权限"
                    :visible.sync="setdialog"
                    width="50%"
                     @close="setdialogclo"
                    >
                   
                    <!-- tree树形控件 -->
                   <el-tree :data="setlist" :props="treeProps" show-checkbox node-key="id"
                                     default-expand-all :default-checked-keys="defkeys" ref="treeRef"></el-tree>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="setdialog = false">取 消</el-button>
                        <el-button type="primary" @click="alllog">确 定</el-button>
                    </span>
            </el-dialog>
           


    </div>
</template>
<script>
export default {
    name:"Roles",
    data(){
        return{
            rolelist:[],
            // 分配权限
            setdialog:false,
            // 权限没有获取失败保存到这里使用
            setlist:[],
            // 树形控件的属性绑定
            treeProps:{
                label:'authName',
                children:'children'
            },
            // 默认选中的节点ID值
            defkeys:[],
            // 分配权限的ID
            rowid:''
            
        }
    },
    mounted(){
        this.getrolelist()
    },
    methods:{
       async getrolelist(){
           let {data:res}=await this.$axios.get("roles")
           console.log(res)
           if(res.meta.status!==200){
               return this.$Message.error("请求失败")
           }
           this.rolelist=res.data
        },
        // 根据ID删除对应的权限
       async remove(role,rightid){
           console.log(role,rightid)
            // 弹框提示
       let confirm=await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).catch(err=>err)
            if(confirm!=confirm){
                return this.$Message.info("取消删除")
            }
           console.log("确认删除")
          let {data:res}=await this.$axios.delete(`roles/${role.id}/rights/${rightid}`)
          console.log(res)
          if(res.meta.status!==200){
              return this.$Message.error("删除权限失败")
          }
            // 刷新局部
            role.chhildren=res.data
        },
        // 展示分配权限的对话框
       async showdialog(role){
           this.rowid=role.id
           let {data:res}=await this.$axios.get("rights/tree")
           console.log(res)
           if(res.meta.status!==200){
               return this.$Message.error("获取权限失败")
           }
           this.setlist=res.data
        //    递归获取三级节点的ID
            this.getkeys(role,this.defkeys)
            // 获取到所有权限的数据
            this.setdialog=true
        },
        // 通过递归获取三级权限的ID
       getkeys(node,arr){
           if(!node.children){
               return arr.push(node.id)
           }
           node.children.forEach(item=>this.getkeys(item,arr))
       },
    //    监听分配对话框的关闭时间
       setdialogclo(){
           this.defkeys=[]
       },
    //    点击为角色分配权限
      async alllog(){
           let keys=[
           ...this.$refs.treeRef.getCheckedKeys(),
           ...this.$refs.treeRef.getHalfCheckedNodes()
           ]
        let idStr=keys.join(",")
        console.log(keys)
        let {data:res}=await this.$axios.post(`roles/${this.rowid}/rights`,{rids:idStr})
         console.log(this.rowid)
         if(res.meta.status!==200){
             return this.$essage.error("更新权限失败")
         }
            this.$message.success("分配权限成功")
            // 刷新页面
            this.getrolelist()
            // 隐藏对话框
            this.setdialog=false
       }
        
    }
}
</script>
<style lang="scss" scoped>
  body,h1,h2,h3,h4,h5,h6,p{
  margin: 0;
  padding: 0;
  list-style: none;
}
body,html,#app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
} 
.el-tag{
    margin: 7px;
}
.bdtop{
    border-top: 1px solid #eee;
}
.bdbottom{
    border-bottom: 1px solid #eee;
}
.el-row{
    display: flex;
    align-items: center;
}
</style>